ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
code {
  white-space: pre-wrap;
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px white inset !important;
}
ul.ant-menu.ant-menu-dark,
.ant-menu-dark .ant-menu-sub {
  background: transparent;
}
ul.ant-menu.ant-menu-dark .ant-menu-item,
.ant-menu-dark .ant-menu-sub .ant-menu-item {
  width: auto;
  background: #00415a;
}
ul.ant-menu.ant-menu-dark .ant-menu-item.ant-menu-item-selected,
.ant-menu-dark .ant-menu-sub .ant-menu-item.ant-menu-item-selected {
  background: #00b8ff;
}
.ant-dropdown {
  display: flex;
  flex-direction: column;
}
.ant-dropdown a,
.ant-dropdown .ant-btn {
  width: 100%;
}
.ant-dropdown a + .ant-btn,
.ant-dropdown .ant-btn + a,
.ant-dropdown .ant-btn + .ant-btn,
.ant-dropdown a + a {
  margin-top: 8px;
}
.table-page {
  padding: 48px 0 52px;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.table-page .ant-table-wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.table-page .ant-table-wrapper .ant-spin-nested-loading {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.table-page .ant-table-wrapper .ant-spin-nested-loading .ant-spin-container {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.table-page .ant-table-wrapper .ant-spin-nested-loading .ant-spin-container .ant-table {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.table-page .ant-table-wrapper .ant-spin-nested-loading .ant-spin-container .ant-table .ant-table-container {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.table-page .ant-table-wrapper .ant-spin-nested-loading .ant-spin-container .ant-table .ant-table-container .ant-table-header {
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: 55px;
}
.table-page .pagination-wrapper {
  display: flex;
  justify-content: flex-end;
  height: 32px;
  margin-top: 10px;
}
.table-page .table-list-buttons {
  display: none;
  white-space: nowrap;
}
.table-page .table-list-buttons a + .ant-btn,
.table-page .table-list-buttons .ant-btn + a,
.table-page .table-list-buttons .ant-btn + .ant-btn,
.table-page .table-list-buttons a + a {
  margin-left: 8px;
}
.table-page .new-item {
  margin-top: -48px;
  margin-bottom: 8px;
  margin-left: auto;
}
.table-page .table-pagination {
  margin: 10px 0 0 auto;
}
@media screen and (min-width: 576px) {
  .table-page .table-list-dropdown {
    display: none;
  }
  .table-page .table-list-buttons {
    display: block;
  }
}
.my-admin {
  display: flex;
  flex-direction: column;
  height: 100%;
}
